import { React, Fragment, useState, useLayoutEffect } from 'react';
import MaterialTable from 'material-table';
import Spinner from '../../../components/UI/Spinner/Spinner';
import axios from 'axios';

const styles = {
  maxWidth: '90%',
  margin: '1em auto',
};

const PartakerList = (props) => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [course, setCourse] = useState('');
  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, []);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/partakers-list/'
    axios
      .get('/partakers-list/' + props.match.params.group)
      .then((response) => {
        setData(response.data);
        setCourse(response.data[0].course);
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  let courseEnrollmentTable = (
    <Fragment>
      <MaterialTable
        title={'Alumnos del curso: ' + course}
        columns={[
          { title: 'Nombre', field: 'name' },
          { title: 'Email', field: 'email' },
        ]}
        data={data}
        options={{
          pageSize: 30,
          pageSizeOptions: [30, 40, 50],
          headerStyle: {
            backgroundColor: '#7395ae',
            color: '#FFF',
            fontSize: '15px',
            fontWeight: 'bold',
          },
          filterCellStyle: {
            backgroundColor: '#01579b',
            color: '#FFF',
          },
          searchFieldStyle: {
            backgroundColor: '#E8E8E8',
            color: 'black',
          },
        }}
        localization={{
          toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'Filas',
            firstAriaLabel: 'Primera pagina',
            firstTooltip: 'Primera pagina',
            previousAriaLabel: 'Pagina anterior',
            previousTooltip: 'Pagina anterior',
            nextAriaLabel: 'Siguiente pagina',
            nextTooltip: 'Siguiente pagina',
            lastAriaLabel: 'Ultima pagina',
            lastTooltip: 'Ultima pagina',
          },
          body: {
            emptyDataSourceMessage: 'No cuenta con cursos asignados',
          },
        }}
      />
    </Fragment>
  );

  if (loading) {
    courseEnrollmentTable = <Spinner />;
  }

  return <div style={styles}>{courseEnrollmentTable}</div>;
};

export default PartakerList;
